* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.home,
.services,
.products,
.sign-up {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.services {
  background-image: url("https://images.unsplash.com/photo-1604596610075-4b2b06b5eb93?ixid=MXwxMjA3fDB8MHx0b3BpYy1mZWVkfDU3OXw2c01WalRMU2tlUXx8ZW58MHx8fA%3D%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.products {
  /*background-image: url("data:image/jpeg;base64,");*/
  background-position: center;
  background-image: url("https://images.unsplash.com/photo-1436162716854-dcb9157bfac1?ixid=MXwxMjA3fDB8MHxzZWFyY2h8MTV8fGFkdmVudHVyZXxlbnwwfHwwfA%3D%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60");
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.sign-up {
  background-image: url("https://images.unsplash.com/photo-1565195160611-251c9f88dc9c?ixid=MXwxMjA3fDB8MHxzZWFyY2h8Nnx8aGlsbCUyMHN0YXRpb258ZW58MHx8MHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}